import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, Alert } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';


import Home from './panels/Home';
import Persik from './panels/Persik';
import Game from './panels/Game';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			l1: 0,
			l2: 0,
			line: 0,
			at:"",
			array:[],
			last:"",
			popout: null,
			v: "v0.9.8.1",
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to, last:"", array:[]})
	};



	result= (e) =>{
		if(this.state.at==="+"){
			var a=this.state.array;
			a[a.length]=(this.state.l1+this.state.line)+" = "+this.state.line+" + "+ this.state.l1;
			var l = a.map((number) =>
				<li>{number}</li>);
			this.setState({last:l})

			this.setState({line:this.state.l1+this.state.line, at:"", l1:0})

		}
		else if(this.state.at==="-"){
			var a=this.state.array;
			a[a.length]=(this.state.l1-this.state.line)+" = "+this.state.line+" - "+ this.state.l1;
			var l = a.map((number) =>
				<li>{number}</li>);
			this.setState({last:l})
			this.setState({line:this.state.l1-this.state.line, at:"", l1:0})
		}
		else if(this.state.at==="*"){
			var a=this.state.array;
			a[a.length]=(this.state.l1*this.state.line)+" = "+this.state.line+" * "+ this.state.l1;
			var l = a.map((number) =>
				<li>{number}</li>);
			this.setState({last:l})
			this.setState({line:this.state.l1*this.state.line, at:"", l1:0})
		}
		else if(this.state.at==="/"){
			var a=this.state.array;
			a[a.length]=(this.state.l1/this.state.line)+" = "+this.state.line+" / "+ this.state.l1;
			var l = a.map((number) =>
				<li>{number}</li>);
			this.setState({last:l})
			this.setState({line:this.state.l1/this.state.line, at:"", l1:0})
		}
		var a=this.state.array;
		if(a.length>7){
			a.splice(0,1)
			this.setState({array:a})
		}

	}
	nabor = (e) => {
		var i=Number(e.currentTarget.dataset.to);

		this.setState({line: this.state.line*10+i});
	}
	sbros=(e)=>{
		this.setState({line:0})
	}
	action= (e) =>{
		this.setState({l1:this.state.line, line:0, at:e.currentTarget.dataset.to})

	}


	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} v={this.state.v}/>
				<Persik id="persik" go={this.go} line={this.state.line} result={this.result} nabor={this.nabor} action={this.action} sbros={this.sbros} last={this.state.last}/>
				<Game id="game" go={this.go} fetchedUser={this.state.fetchedUser} f={this.state.activePanel}/>
			</View>
		);
	}
}

export default App;
