import React from 'react';
import PropTypes from 'prop-types';
import {Panel, ListItem, Button, Group, Div, Avatar, PanelHeader, Alert, View} from '@vkontakte/vkui';
import connect from "@vkontakte/vkui-connect";



class Home extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			l1: 0,
			l2: 0,
			line: 0,
			at:"",
			array:[],
			last:"",
			popout: null,
			v: "v0.9.5",
		};
	}
	pr(){
		this.setState({ popout:
				<Alert
					actions={[{
						title: 'ОК',
						autoclose: true,
						style: 'cancel'
					}, ]}
					onClose={this.closePopout.bind(this)}
				>
					<h2>Завершение</h2>
					<p>Детали конструктора Lego придут Вам на почту в течение 999999 лет))</p>
				</Alert>
		});
	}
	not(){
		if (connect.supports("VKWebAppAllowNotifications")) {
			connect.send("VKWebAppAllowNotifications", {});

		}
	}
	openDefault(){

		this.setState({ popout:
				<Alert
					actions={[{
						title: 'Отмена',
						autoclose: true,
						style: 'cancel'
					}, {
						title: 'Забрать',
						autoclose: false,
						action:this.pr.bind(this)
					}]}
					onClose={this.closePopout.bind(this)}
				>
					<h2>Подтвердите действие</h2>
					<p>Вы выиграли автомобиль! Нажмите на кнопку чтобы забрать его.</p>
				</Alert>
		});
	}
	closePopout () {
		this.setState({ popout: null })
	}


	render() {
		const {id, fetchedUser, go, v} = this.props;

		return (
			<View popout={this.state.popout} header={false} activePanel={id}>
				<Panel id={id}>

					<PanelHeader>Example</PanelHeader>

					{fetchedUser &&
					<Group title="">
						<ListItem
							before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
							description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
						>
							Привет, {`${fetchedUser.first_name}`}!
						</ListItem>

					</Group>}

					<Group>
						<Div>
							<Button size="xl" level="2"  onClick={go} data-to="persik" >
								Перейти к калькулятору
							</Button>
                            <Button size="xl" level="2" onClick={go} data-to="game" style={{marginTop:5}} >
                                Крестики-нолики
                            </Button>
							<Button size="xl" level="2" onClick={this.not.bind(this)} style={{marginTop:5}}>
								Подписаться на уведомления
							</Button>

							<Button size="xl" level="2" onClick={this.openDefault.bind(this)}style={{marginTop:5}}>
								Тут секрет
							</Button>
						</Div>
					</Group>
					<Div style={{textAlign: 'center', fontSize:12}}>{v}</Div>



				</Panel>
			</View>
		);
	}

}





Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;